<template>
  <div class="container-fluid pt-2">
    <div class="row" :class="{
      'section-loading': isLoading_1
    }">
      <div class="col-2 text-center">
        <level :lv="CustInfo.lv" />
        <br/>
        <avatar :gender="Number(CustInfo.gender)" />
        <h3 class="mt-0">
          <small v-if="CustInfo.birthday !== ''">
            <b>{{ CustInfo.age? CustInfo.age : '— —' }}歲</b>
          </small>
          <small v-else>
            <b>— —</b>
          </small>
        </h3>
      </div>
      <div class="col-10">
        <div class="row">
          <div class="col-3">
            <div class="item row">
              <div class="col-3 label">姓名</div>
              <div class="col-9 text d-flex">
                <div class="name-block">{{CustInfo.name}}</div>
                <a class="link text-underline all-name"
                  v-show="CustInfo.name && CustInfo.name.length > 10"
                  tabindex="0"
                  id="user-name"
                  data-toggle="popover"
                  role="button"
                  :data-content="CustInfo.name">
                  看全名
                </a>
              </div>
            </div>
          </div>
          <div class="col-6">
            <div class="item row" v-show="CustInfo.wg_name && CustInfo.wg_name!==''">
              <div class="col-2 label">原住民名</div>
              <div class="col-10 text d-flex">
                <div style="line-height:20px" class="name-block">{{CustInfo.wg_name}}</div>
                <a class="link text-underline all-name"
                  v-show="CustInfo.wg_name && CustInfo.wg_name.length > 20"
                  tabindex="1"
                  id="aboriginal-name"
                  data-toggle="popover"
                  role="button"
                  :data-content="CustInfo.wg_name">
                  看全名
                </a>
              </div>
            </div>
          </div>
          <!-- <div>
          </div> -->
          <div class="col-3 text-right action">
            <a v-if="custDieStatus || source6 == '1' || this.$store.state.custInfo.ID === ''" 
            class="btn btn-default btn-sm mb-0 mt-1" 
            @click="panelOpen('confirmDeleteOpen')">
              刪除客戶
            </a>
            <a @click="panelOpen('contactInfoOpen')" class="btn btn-default btn-sm btn-icon mb-0 mt-1">
              <i class="icon-edit i-18"></i>
            </a>
          </div>
        </div>
        <div class="row">
          <div class="col-3">
            <div class="item row">
              <div class="col-3 label">性別</div>
              <div class="col-9 text">
                <span v-if="CustInfo.gender == 2">女</span>
                <span v-else-if="CustInfo.gender == 1">男</span>
                <span v-else>— —</span>
              </div>
            </div>
            <div class="item row">
              <div class="col-3 label pl-0">ID</div>
              <div class="col-9 text">{{CustInfo.ID | emptyField}}</div>
            </div>
            <div class="item row">
              <div class="col-3 label">生日</div>
              <div class="col-9 text">{{CustInfo.birthday | dateFormat}}</div>
            </div>
          </div>
          <div class="col-3">
            <div class="item row">
              <div class="col-4 label">手機</div>
              <div class="col-8 text">{{CustInfo.mobile | emptyField}}</div>
            </div>
            <div class="item row">
              <div class="col-4 label pl-0">住家電話</div>
              <div class="col-8 text">{{CustInfo.phone | phoneFormat | emptyField}}</div>
            </div>
            <div class="item row">
              <div class="col-4 label pl-0">公司電話</div>
              <div class="col-8 text">{{CustInfo.office_phone | phoneFormat | emptyField}}</div>
            </div>
          </div>
          <div class="col-6">
            <div class="item row">
              <div class="col-3 label pl-0">LINE ID</div>
              <div class="col-9 text">{{CustInfo.lineid | emptyField}}</div>
            </div>
            <div class="item row">
              <div class="col-3 label">現居地址</div>
              <div class="col-9 text">
                <span v-if="!CustInfo.address[1] && !CustInfo.address[2] && !CustInfo.address[3]">— —</span>
                <a class="link text-underline"
                target="_blank"
                :href="`https://www.google.com/maps/place/${CustInfo.address}`"
                v-else>
                  {{CustInfo.address | addressFormat | emptyField}}
                </a>
              </div>
            </div>
            <div class="item row">
              <div class="col-3 label">公司地址</div>
              <div class="col-9 text">
                <span v-if="!CustInfo.office_address[1] && !CustInfo.office_address[2] && !CustInfo.office_address[3]">— —</span>
                <a class="link text-underline"
                target="_blank"
                :href="`https://www.google.com/maps/place/${CustInfo.office_address}`"
                v-else>
                  {{CustInfo.office_address | addressFormat | emptyField}}
                </a>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>

    <contact-info-modal
    :open="contactInfoOpen"
    :inputs="CustInfo"
    @submit="contactSubmit"
    @close="panelClose('contactInfoOpen')"/>

    <confirm-delete
    :open="confirmDeleteOpen"
    :input-data="CustInfo"
    @close="panelClose('confirmDeleteOpen')"
    @confirm="deleteCustomer"/>
  </div>
</template>

<script>
import { mapActions, mapGetters } from 'vuex';
import { commonMixin } from '../../../mixins/common';
import hanlinkMixin  from '../../../mixins/hanlink.Mixin'
import Avatar from '../../components/Avatar';
import Level from '../../components/Level';
import ContactInfoModal from '../ContactInfoModal';
import ConfirmDelete from '../ConfirmDelete';
import { deleteCust,getCustInfo,editCustInfo } from '../../../service';
import { log } from 'util';

export default {
  name: 'CustomerInfo',
  mixins: [commonMixin, hanlinkMixin],
  props: {
    loading: {
      type: Boolean,
      default: false
    },
    custDieStatus: {
      type: Boolean,
      default: false
    },
    source6: {
      type:[String,Number],
      default: '',
    }
  },
  data () {
    return {
      isLoading_1: this.loading,
      contactInfoOpen: false,
      confirmDeleteOpen: false
    }
  },
  computed: {
    ...mapGetters(['BaseUrl', 'CustInfo'])
  },
  watch: {
    'loading' : function() {
      this.isLoading_1 = this.loading
      
    }
  },
  components: {
    Avatar, Level, ContactInfoModal, ConfirmDelete
  },
  methods: {
    ...mapActions([
      'setPageLock' , 'updateCustInfo'
    ]),
    setCustInfo(user) {
      let custInfo = {
        lv: Number(user.vip_lvl || '0'),
        familyLeader: false,
        name: user.name || '',
        gender: user.sex || 0,
        birthday: user.birthday || '',
        ID: user.id || '',
        age: user.age || '',
        serNo: user.ser_no || '',
        lineid: user.lineid || '',
        mobile: user.mobile? user.mobile.trim() : '',
        phone: [user.home_tel_area? user.home_tel_area.trim() : '', user.home_tel? user.home_tel.trim() : ''],
        office_phone: [user.corp_tel_area? user.corp_tel_area.trim() : '', user.corp_tel? user.corp_tel.trim() : '', user.corp_tel_ext? user.corp_tel_ext.trim() : ''],
        fax: [user.fax_area? user.fax_area.trim() : "", user.fax? user.fax.trim() : ""],
        // fax: '',
        address: [user.home_addr_zip? user.home_addr_zip.trim() : '', user.home_addr1? user.home_addr1.trim() : '', user.home_addr2? user.home_addr2.trim() : '', user.home_addr3? user.home_addr3.trim() : ''],
        office_address: [user.corp_addr_zip? user.corp_addr_zip.trim() :'', user.corp_addr1? user.corp_addr1.trim() :'', user.corp_addr2? user.corp_addr2.trim() :'', user.corp_addr3? user.corp_addr3.trim() :''],
        wg_name: user.wg_name || '',
      }
      
      this.updateCustInfo(custInfo)

    },
    contactSubmit(custInfo) {
      this.panelClose('contactInfoOpen')
      this.isLoading_1 = true
      console.log(custInfo);
      
      let custNo = this.$store.state.custInfo.ID || ''
      editCustInfo(this.$store.state.userInfo.id, custNo, custInfo, null).then(result => {
        if(result.header && result.header.status === '200') {
          // this.updateCustInfo(custInfo)
          getCustInfo(this.$store.state.userInfo.id, this.$store.state.userInfo.id, (custInfo.ID || custInfo.serNo)).then(result => {
            this.setCustInfo(result.body.customer)
            this.isLoading_1 = false
            this.$toast.info({title: '「客戶資料」已更新'})
          })
          
        } else {
          this.isLoading_1 = false
          if(result.header.message) {
            this.$toast.info({title: result.header.message})
          } else {
            this.$toast.info({title: '「客戶資料」更新失敗'})
          }
        }
      })
    },
    deleteCustomer () {
      //custNo 後續要加判斷是否為無ID客戶

      let custNo = this.$store.state.custInfo.ID || this.$store.state.custInfo.serNo

      deleteCust(this.$store.state.userInfo.id, custNo, "").then(result =>{
        if(result && result.header.status == '200'){
          this.panelClose('confirmDeleteOpen')
          this.$toast.info({title: `已刪除「${this.CustInfo.name}」`})
          this.$router.push({path:`/`})
        }
        else{
          this.$toast.info({title: `錯誤「${result.header.message}」`})
          this.panelClose('confirmDeleteOpen')
        }
      })   
      
    },
  },
  mounted () {

    window.$('#user-name').popover({
      trigger: 'focus',
      placement: 'bottom'
    })
    window.$('#aboriginal-name').popover({
      trigger: 'focus',
      placement: 'bottom'
    })

    // if ( "function" === typeof(parseEUDC) ) { 目前顯示難字仍有問題, 暫時不用
    //   parseEUDC("ap");
    // }

  }
}
</script>

