export default {
  data() {
    return {};
  },
  created() {
    window.imeServer = "https://mi.cathaylife.com.tw/wf/";
    window.WebRootPath =
      process.env.NODE_ENV === "production"
        ? `./${this.$conf.System}/js/`
        : `/crm/js/`;
  },
  mounted() {
    this.registerHanlink();
  },
  methods: {
    registerHanlink() {
      var el = document.getElementsByClassName("ime-window");
      if (el.length > 0) {
        for (var node of el) {
          node.parentNode.removeChild(node);
        }
      }
      
      let jscript = document.createElement("script");
      jscript.setAttribute("lang", "text/javascript");
      jscript.setAttribute(
        "src",
        window.WebRootPath + "hanlinks/wf_links.min.js"
      );
      document
        .getElementsByTagName("head")
        .item(0)
        .appendChild(jscript);
    }
  }
};
